    position: absolute;
    left: 93%;
    top: 4%;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
   
    
  }

  .menu-bars1:hover{
   
      -webkit-transform: scale(2.0);
   
              transform: scale(2.0);
      box-shadow: 0px 0px 30px rgba(0,0,0,0.1);
    
  }
  
  
